import { Component, Fragment, Prop, State, h } from "@stencil/core";

@Component({
  tag: "pace-select",
  shadow: false,
  styleUrl: "select.scss",
})
export class SelectComponent {
  @Prop() label: string;
  @Prop() name: string;
  @Prop() value: string | number;
  @Prop() options: { value: string | number; label: string }[];
  @Prop() onChange: (e: any) => void;
  @Prop() onInput: (e: any) => void;
  @Prop() searchable: boolean;
  @Prop() readonly: boolean;
  @Prop() loading: boolean;
  @Prop() required?: boolean;
  @Prop() selectStyle?: {
    input: { [key: string]: string };
    options: { [key: string]: string };
    selectedOption: { [key: string]: string };
    highlightedOption: { [key: string]: string };
  } = { input: {}, options: {}, selectedOption: {}, highlightedOption: {} };

  @State() hideOptions = true;
  @State() selectedOptionIdx = 0;

  private lockOptions = false;
  private optionElements: HTMLLIElement[] = [];
  private textElement: HTMLInputElement;

  resetOptions() {
    this.lockOptions = false;
    this.hideOptions = true;
  }

  handleOptionClick(value: string | number) {
    this.onChange({ target: { value: value } });
    this.resetOptions();
  }

  componentDidRender() {
    if (this.hideOptions) {
      this.textElement.value =
        this.options.find((o) => o.value === this.value)?.label ?? "";
    }
  }

  private scollElementIntoView(element: HTMLElement) {
    if (element) {
      element.scrollIntoView({
        block: "center",
        inline: "center",
      });
    }
  }

  handleKeyDown(e: KeyboardEvent) {
    if (this.readonly) return;
    const options = this.options;
    const selectedOptionIdx = this.selectedOptionIdx;
    switch (e.key) {
      case "ArrowDown":
        e.preventDefault();
        if (selectedOptionIdx < options.length - 1) {
          this.selectedOptionIdx = selectedOptionIdx + 1;
        }
        this.scollElementIntoView(this.optionElements[selectedOptionIdx]);
        break;
      case "ArrowUp":
        e.preventDefault();
        if (selectedOptionIdx > 0) {
          this.selectedOptionIdx = selectedOptionIdx - 1;
        }
        this.scollElementIntoView(this.optionElements[selectedOptionIdx]);
        break;
      case "Enter":
        e.preventDefault();
        this.handleOptionClick(options[selectedOptionIdx].value);
        break;
      case "Escape":
        e.preventDefault();
        this.resetOptions();
        break;
      default:
        break;
    }
  }

  handleInputOnBlur() {
    if (!this.lockOptions) {
      this.hideOptions = true;
    }
  }

  handleInputOnFocus() {
    this.hideOptions = false;
    this.textElement.select();
  }

  handleOnInput(e: Event) {
    if (this.hideOptions) {
      this.hideOptions = false;
    }
    this.selectedOptionIdx = 0;
    this.onInput(e);
  }

  getOptionHighlightStyle(option) {
    const isSelected = option.value === this.value,
      isHighlighted = this.options.indexOf(option) === this.selectedOptionIdx;

    if (isHighlighted) {
      return this.selectStyle.highlightedOption;
    }
    if (isSelected) {
      return this.selectStyle.selectedOption;
    }
    return {};
  }

  render() {
    return (
      <Fragment>
        <label htmlFor={this.name}>{this.label}</label>
        <div class="select-wrapper">
          <input
            style={this.selectStyle.input}
            autoComplete="off"
            placeholder="Type to get suggestions..."
            readOnly={!this.searchable || this.readonly}
            ref={(el) => {
              this.textElement = el;
            }}
            type="text"
            id={this.name}
            name={this.name}
            required={this.required}
            onFocus={() => this.handleInputOnFocus()}
            onBlur={() => this.handleInputOnBlur()}
            onInput={(e) => this.handleOnInput(e)}
            onKeyDown={(e) => this.handleKeyDown(e)}
          />
          <div
            class="select-arrow"
            style={{
              position: "absolute",
            }}
          >
            {this.readonly || (
              <svg
                height="20"
                width="20"
                viewBox="0 0 20 20"
                aria-hidden="true"
                focusable="false"
                class="css-8mmkcg"
                onClick={() => {
                  this.textElement.focus();
                }}
              >
                <path d="M4.516 7.548c0.436-0.446 1.043-0.481 1.576 0l3.908 3.747 3.908-3.747c0.533-0.481 1.141-0.446 1.574 0 0.436 0.445 0.408 1.197 0 1.615-0.406 0.418-4.695 4.502-4.695 4.502-0.217 0.223-0.502 0.335-0.787 0.335s-0.57-0.112-0.789-0.335c0 0-4.287-4.084-4.695-4.502s-0.436-1.17 0-1.615z"></path>
              </svg>
            )}
            {!this.loading || (
              <svg
                width="24"
                height="24"
                viewBox="0 0 24 24"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  d="M12,1A11,11,0,1,0,23,12,11,11,0,0,0,12,1Zm0,19a8,8,0,1,1,8-8A8,8,0,0,1,12,20Z"
                  opacity=".25"
                />
                <path
                  d="M10.14,1.16a11,11,0,0,0-9,8.92A1.59,1.59,0,0,0,2.46,12,1.52,1.52,0,0,0,4.11,10.7a8,8,0,0,1,6.66-6.61A1.42,1.42,0,0,0,12,2.69h0A1.57,1.57,0,0,0,10.14,1.16Z"
                  class="spinner_ajPY"
                />
              </svg>
            )}
          </div>
          {this.readonly || (
            <ul
              class="select-options"
              style={this.selectStyle.options}
              hidden={this.hideOptions}
              onMouseEnter={() => (this.lockOptions = true)}
              onMouseLeave={() => (this.lockOptions = false)}
            >
              {this.options.map((option) => (
                <li
                  ref={(el) =>
                    (this.optionElements[this.options.indexOf(option)] = el)
                  }
                  key={option.value}
                  class={{
                    selected: option.value === this.value,
                    highlighted:
                      this.options.indexOf(option) === this.selectedOptionIdx,
                  }}
                  style={this.getOptionHighlightStyle(option)}
                  onClick={() => this.handleOptionClick(option.value)}
                  onMouseEnter={() => {
                    this.selectedOptionIdx = this.options.indexOf(option);
                  }}
                >
                  {option.label}
                </li>
              ))}
            </ul>
          )}
        </div>
      </Fragment>
    );
  }
}
