@import "src/globals/common.scss";

.select-wrapper {
  position: relative;
  box-sizing: border-box;

  input {
    box-sizing: border-box;
    width: 100%;
  }

  ul.select-options {
    list-style: none;
    border: 1px solid $primaryBackgroundColor;
    background-color: $secondaryBackgroundColor;
    position: absolute;
    margin: 0;
    padding: 0;
    width: 100%;
    z-index: 9999;
    max-height: 200px;
    overflow-y: auto;
    text-align: left;
    li {
      padding: 10px;
      cursor: pointer;
      &.selected {
        background-color: $primaryBackgroundColor;
      }
      &.highlighted {
        background-color: $selectedColor;
      }
    }

    /* width */
    &::-webkit-scrollbar {
      width: 10px;
    }

    /* Track */
    &::-webkit-scrollbar-track {
      background: transparent;
    }

    /* Handle */
    &::-webkit-scrollbar-thumb {
      background: #aaa;
      border-radius: 5px;
    }

    /* Handle on hover */
    &::-webkit-scrollbar-thumb:hover {
      background: #555;
    }
  }

  .select-arrow {
    position: absolute;
    top: 30%;
    right: 10px;
  }

  .spinner_ajPY {
    transform-origin: center;
    animation: spinner_AtaB 0.75s infinite linear;
  }
  @keyframes spinner_AtaB {
    100% {
      transform: rotate(360deg);
    }
  }
}
